import { Links } from "../../../content";
import { SpacerAtom, TextAtom } from "../../atoms";
import { ContactFormMolecule, FooterMolecule, ListMolecule, PhoneNumbersMolecule } from "../../molecules";

export function FooterOrganism() {
  const links: JSX.Element = (
    <ListMolecule
      unstyled
      bold
      links={[
        {
          ...Links.intern.start,
          selected:
            window.location.hash.startsWith(Links.intern.start.href) ||
            window.location.hash === "" ||
            window.location.hash === "/home",
        },
        {
          ...Links.intern.fysiotherapie,
          selected:
            window.location.hash.startsWith(Links.intern.fysiotherapie.href) ||
            window.location.hash === "/fysiotherapie",
        },
        {
          ...Links.intern.pijn,
          selected: window.location.hash.startsWith(Links.intern.pijn.href) || window.location.hash === "/pijn",
        },
        // {
        //   ...Links.intern.revalidatie,
        //   selected:
        //     window.location.hash.startsWith(Links.intern.revalidatie.href) ||
        //     window.location.hash === "/revalidatie",
        // },
        // {
        //   ...Links.intern.prestatie,
        //   selected:
        //     window.location.hash.startsWith(Links.intern.prestatie.href) ||
        //     window.location.hash === "/prestatie",
        // },
        // {
        //   ...Links.intern.leefstijl,
        //   selected:
        //     window.location.hash.startsWith(Links.intern.leefstijl.href) ||
        //     window.location.hash === "/leefstijl",
        // },
        {
          ...Links.intern.team,
          selected: window.location.hash.startsWith(Links.intern.team.href) || window.location.hash === "/team",
        },
        {
          ...Links.intern.contact,
          selected: window.location.hash.startsWith(Links.intern.contact.href) || window.location.hash === "/tarieven",
        },
      ]}
    />
  );

  const locaties: JSX.Element = (
    <>
      <div>
        <TextAtom children={"Amsterdam IJburg"} />
        <TextAtom children={"Diemerparklaan 35"} />
      </div>
      <SpacerAtom space={8} />
      <div>
        <TextAtom children={"Almere-Duin"} />
        <TextAtom children={"Zeeduinweg 560A"} />
      </div>
    </>
  );

  const telefoonnummers: JSX.Element = <PhoneNumbersMolecule />;

  const contact: JSX.Element = <ContactFormMolecule />;

  return (
    <FooterMolecule
      titles={["Menu", "Locaties", "Telefoon", "E-mail ons"]}
      lists={[links, locaties, telefoonnummers, contact]}
    />
  );
}
