import { Teksten } from "../../../content";
import { HeadingH3Atom, SpacerAtom, TextAtom } from "../../atoms";
import {
  BackgroundBlockMolecule,
  CarouselMolecule,
  ImageblockMolecule,
  ImageJumbotronMolecule,
} from "../../molecules";
import { SloganOrganism } from "../slogan";

export function FysiotherapieOrganism() {
  return (
    <>
      <ImageblockMolecule
        src={"assets/images/DSC_7776.jpg"}
        alt={"image"}
        title={Teksten.fysiotherapie.title}
        text={Teksten.fysiotherapie.text}
        button={{
          href: "/contact",
          text: "Neem contact met ons op",
          title: "Neem contact met ons op",
        }}
      />
      <BackgroundBlockMolecule
        children={
          <HeadingH3Atom children={Teksten.werkwijze.text} color="zand" />
        }
        background={"paars"}
      />
      <ImageJumbotronMolecule
        src={"assets/images/DSC_7363.jpg"}
        alt={"image"}
        title={Teksten.aandacht.title}
        text={Teksten.aandacht.text}
        color="lichtBlauw"
      />
      <SloganOrganism slogan={Teksten.slogan4.title} />
      <ImageJumbotronMolecule
        src={"assets/images/DSC_6977.jpg"}
        alt={"image"}
        title={Teksten.aanpak.title}
        text={Teksten.aanpak.text}
        color="lichtBlauw"
        rtl
      />
      <CarouselMolecule
        items={[
          <div>
            <HeadingH3Atom>{Teksten.conclusie.title}</HeadingH3Atom>
            <SpacerAtom space={4} />
            <TextAtom>{Teksten.conclusie.text}</TextAtom>
          </div>,
          <div>
            <HeadingH3Atom>{Teksten.behandeling.title}</HeadingH3Atom>
            <SpacerAtom space={4} />
            <TextAtom>{Teksten.behandeling.text}</TextAtom>
          </div>,
          <div>
            <HeadingH3Atom>{Teksten.maatwerk.title}</HeadingH3Atom>
            <SpacerAtom space={4} />
            <TextAtom>{Teksten.maatwerk.text}</TextAtom>
          </div>,
        ]}
      />
    </>
  );
}
