import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { ListMolecule } from "..";
import {
  ContainerAtom,
  DesktopHiddenAtom,
  HeaderAtom,
  ImageAtom,
  LinkAtom,
  ListItemAtom,
  MobileHiddenAtom,
} from "../../atoms";
import { ILink } from "../../interfaces/ILink";
import { ButtonMolecule, IconButtonMolecule } from "../button";
import styles from "./styles.module.scss";

interface Props {
  links: ILink[];
  logo: string;
}

export function HeaderMolecule(props: Props) {
  const [windowWidth] = useState(window.innerWidth);
  const desktopBreakpoint = 16 * 50; // Should reference breakpoints.scss

  const [menuOpened, setMenuOpened] = useState(false);
  const navigate = useNavigate();

  return (
    <HeaderAtom>
      <div className={styles.topbalk}>
        <ContainerAtom centeredHorizontal centeredVertical type="between" smallGab>
          <div onClick={() => navigate("/home")}>
            <ImageAtom alt="logo" size={windowWidth <= desktopBreakpoint ? "auto" : "xlarge"} src={props.logo} />
          </div>
          <DesktopHiddenAtom>
            <IconButtonMolecule
              id="icon-hamburger"
              title={"Menu"}
              type="default"
              icon={"assets/icons/icon-hamburger-white.svg"}
              onClick={() => {
                setMenuOpened(!menuOpened);
              }}
            />
          </DesktopHiddenAtom>
          <MobileHiddenAtom>
            <nav>
              <ContainerAtom type="between">
                {props.links.map((item) => (
                  <ListItemAtom key={item.text} bold>
                    <LinkAtom {...item} bold />
                  </ListItemAtom>
                ))}
              </ContainerAtom>
            </nav>
          </MobileHiddenAtom>
          <MobileHiddenAtom>
            <div>
              <ButtonMolecule
                text={"Afspraak maken"}
                id={"afspraak_maken"}
                title={"Afspraak maken"}
                onClick={() => navigate("/contact")}
                type={"default"}
              />
            </div>
          </MobileHiddenAtom>
        </ContainerAtom>
        <DesktopHiddenAtom>
          <nav>
            <ContainerAtom type="row">
              <div className={styles.mobile_nav} onClick={() => setMenuOpened(false)}>
                {menuOpened && <ListMolecule unstyled bold links={props.links} />}
              </div>
            </ContainerAtom>
          </nav>
        </DesktopHiddenAtom>
      </div>
    </HeaderAtom>
  );
}
