import { Teksten } from "../../../content";
import { IImageText } from "../../interfaces/IJumbotronImage";
import { PijnMolecule } from "../../molecules";

export function PijnOrganism() {
  const items: IImageText[] = [
    {
      src: "assets/images/DSC_6977.jpg",
      alt: "image",
      title: Teksten.pijn.title,
      text: Teksten.pijn.text,
      color: "lichtBlauw",
    },
    {
      src: "assets/images/DSC_7877.jpg",
      alt: "image",
      title: Teksten.uitlegpijn.title,
      text: Teksten.uitlegpijn.text,
      color: "lichtBlauw",
    },
    {
      src: "assets/images/DSC_6988.jpg",
      alt: "image",
      title: Teksten.aanpak.title,
      text: Teksten.aanpak.text,
      color: "lichtBlauw",
    },
  ];

  const slogans: string[] = [Teksten.slogan2.title, Teksten.slogan3.title];

  return <PijnMolecule info={items} slogan={slogans} />;
}
