import styles from "./styles.module.scss";

interface Props {
  src: string;
  size: "icon" | "small" | "medium" | "large" | "xlarge" | "xxlarge" | "auto";
  alt: string;
}

export function ImageAtom(props: Props) {
  let className = styles.logo;

  switch (props.size) {
    case "icon":
      className += " " + styles.logo__icon;
      break;
    case "small":
      className += " " + styles.logo__small;
      break;

    case "medium":
      className += " " + styles.logo__medium;
      break;

    case "large":
      className += " " + styles.logo__large;
      break;

    case "xlarge":
      className += " " + styles.logo__xlarge;
      break;

    case "xxlarge":
      className += " " + styles.logo__xxlarge;
      break;

    case "auto":
      className += " " + styles.logo__auto;
      break;
  }

  return (
    <img className={className} alt={props.alt} src={props.src} loading="lazy" />
  )
}
