import { ContainerAtom, HeadingH1Atom } from "../../atoms";
import { IImageText } from "../../interfaces/IJumbotronImage";
import { BackgroundBlockMolecule } from "../background-block";
import { JumbotronMolecule } from "../jumbotron";
import styles from "./styles.module.scss";

interface Props {
  team: IImageText[];
}

export function TeamSectionMolecule(props: Props) {
  return (
    <BackgroundBlockMolecule children={
      <div className={styles.content}>
        <HeadingH1Atom children={"Onze specialisten"} color="zand" />
        {/* <SpacerAtom space={25} /> */}
        {/* <div className={styles.werknemers}> */}
        <ContainerAtom centeredHorizontal type="row">
          {props.team.map((member: IImageText) => {
            return (
              <JumbotronMolecule
                key={member.title}
                title={member.title}
                image={member.src}
              />
            );
          })}
        </ContainerAtom>
        {/* </div> */}
        {/* <SpacerAtom space={16} /> */}
      </div>
    } background={"paars"} />
  );
}
