import { Teksten } from "../../../content";
import { ContainerAtom, HeadingH2Atom, HeadingH3Atom, LinkAtom, SpacerAtom, TextAtom } from "../../atoms";
import { ContactFormMolecule } from "../contact-form";
import { JumbotronMolecule } from "../jumbotron";
import { PhoneNumbersMolecule } from "../phone-numbers";
import styles from "./styles.module.scss";

export function ContactMolecule() {
  return (
    <div className={styles.container}>
      <ContainerAtom type="columns">
        <ContainerAtom type="row">
          <ContainerAtom type="columns">
            <HeadingH2Atom children={"Contact gegevens"} />
            <div>
              <HeadingH3Atom children={"Telefoonnummers"} />
              <PhoneNumbersMolecule />
            </div>
            <div>
              <HeadingH3Atom children={"Amsterdam"} />
              <TextAtom children={"Amsterdam IJburg"} />
              <TextAtom children={"Diemerparklaan 35"} />
            </div>
            <div>
              <HeadingH3Atom children={"Almere"} />
              <TextAtom children={"Almere-Duin"} />
              <TextAtom children={"Zeeduinweg 560A"} />
            </div>
          </ContainerAtom>
          <ContainerAtom type="columns">
            <HeadingH2Atom children={"Stuur ons en e-mail"} />
            <ContactFormMolecule />
          </ContainerAtom>
        </ContainerAtom>
        <SpacerAtom space={16} />
        <ContainerAtom type="row">
          <JumbotronMolecule title={Teksten.ov.title} color="lichtBlauw" text={Teksten.ov.text} />
          <JumbotronMolecule title={Teksten.fiets.title} color="lichtBlauw" text={Teksten.fiets.text} />
          <JumbotronMolecule
            title={Teksten.auto.title}
            color="lichtBlauw"
            text={Teksten.auto.text}
            extra={
              <LinkAtom href={"https://maps.app.goo.gl/JYhmRaT42cSriVu47"} text={"locatie parkeergarage"} external />
            }
          />
        </ContainerAtom>
      </ContainerAtom>
    </div>
  );
}
