import { useNavigate } from "react-router-dom";
import {
  ContainerAtom,
  HeadingH1Atom,
  ImageAtom,
  SpacerAtom,
  TextAtom,
} from "../../atoms";
import { ILink } from "../../interfaces/ILink";
import { ButtonMolecule } from "../button";
import styles from "./styles.module.scss";

interface Props {
  src: string;
  alt: string;
  button: ILink;
  title?: string;
  text?: string;
}

export function ImageblockMolecule(props: Props) {
  const navigate = useNavigate();

  return (
    <div className={styles.container}>
      <ContainerAtom type="between" centeredVertical>
        <div className={styles.text}>
          <HeadingH1Atom children={props.title} />
          <SpacerAtom space={4} />
          <TextAtom>{props.text}</TextAtom>
          <SpacerAtom space={8} />
          <div className={styles.flex}>
            <ButtonMolecule
              text={props.button.text}
              title={props.button.title ?? ""}
              onClick={() => {
                navigate(props.button.href);
              }}
              type={"primary"}
              id={"Button_ImageBlock"}
            />
          </div>
        </div>
        <div>
          <ImageAtom src={props.src} size={"xxlarge"} alt={"image"} />
        </div>
      </ContainerAtom>
    </div>
  );
}
