import { IImageText } from "../../interfaces/IJumbotronImage";
import { SloganOrganism } from "../../organisms";
import { ImageJumbotronMolecule } from "../image-jumbotron";

interface Props {
  info: IImageText[];
  slogan: string[];
}

export function PijnMolecule(props: Props) {
  return (
    <>
      {props.info.map((info: IImageText, index: number) => {
        return (
          <>
            <ImageJumbotronMolecule
              src={info.src}
              alt={info.alt}
              title={info.title}
              text={info.text}
              size={"auto"}
              color="lichtBlauw"
              rtl={index % 2 === 0}
            />

            {props.slogan[index] && (
              <SloganOrganism slogan={props.slogan[index]} />
            )}
          </>
        );
      })}
    </>
  );
}
