import { useState } from "react";
import { LabelAtom, TextInputAtom } from "../../atoms";
import styles from "./styles.module.scss";

interface Props {
  id?: string;
  placeholder?: string;
  onChange?: (value: string) => void;
  onBlur?: (value: React.ChangeEvent<HTMLInputElement>) => void;
  onEnter?: () => void;
  value?: string;
  defaultValue?: string;
  label?: string;
}

interface InputProps extends Props {
  type: "text" | "email";
}

function InputMolecule(props: InputProps) {
  const [hasBlurred, setHasBlurred] = useState(false);
  const onBlur = (value: React.ChangeEvent<HTMLInputElement>) => { setHasBlurred(true); if (props.onBlur) props.onBlur(value); };

  const textInputField = (
    <TextInputAtom
      {...props}
      onBlur={onBlur}
      onChange={props.onChange}
    />
  );

  return (
    <div className={styles.container}>
      {props.label && <LabelAtom id={props.label} children={props.label} type={"default"} bold />}
      {textInputField}
    </div>
  );
}

export function EmailInputMolecule(props: Props) {
  return <InputMolecule {...props} type="email" />;
}

export function TextInputMolecule(props: Props) {
  return <InputMolecule {...props} type="text" />;
}