import { Teksten } from "../../../content";
import { ImageblockMolecule } from "../../molecules/imageblock";

export function HomeImageOrganism() {
  return (
    <ImageblockMolecule
      src={"assets/images/DSC_7904.jpg"}
      alt={"image"}
      title={Teksten.home.title}
      text={Teksten.home.text}
      button={{
        href: "/team",
        text: "Lees meer over onze visie",
        title: "Lees meer over onze visie",
      }}
    />
  );
}
