import { Links } from "../../../content";
import { ILink } from "../../interfaces/ILink";
import { HeaderMolecule } from "../../molecules/header";

export function HeaderOrganism() {
  const links: ILink[] = [
    {
      ...Links.intern.start,
      selected:
        window.location.hash.startsWith(Links.intern.start.href) ||
        window.location.hash === "" ||
        window.location.hash === "/home",
    },
    {
      ...Links.intern.fysiotherapie,
      selected:
        window.location.hash.startsWith(Links.intern.fysiotherapie.href) ||
        window.location.hash === "/fysiotherapie",
    },
    {
      ...Links.intern.pijn,
      selected:
        window.location.hash.startsWith(Links.intern.pijn.href) ||
        window.location.hash === "/pijn",
    },
    // {
    //   ...Links.intern.revalidatie,
    //   selected:
    //     window.location.hash.startsWith(Links.intern.revalidatie.href) ||
    //     window.location.hash === "/revalidatie",
    // },
    // {
    //   ...Links.intern.prestatie,
    //   selected:
    //     window.location.hash.startsWith(Links.intern.prestatie.href) ||
    //     window.location.hash === "/prestatie",
    // },
    // {
    //   ...Links.intern.leefstijl,
    //   selected:
    //     window.location.hash.startsWith(Links.intern.leefstijl.href) ||
    //     window.location.hash === "/leefstijl",
    // },
    {
      ...Links.intern.team,
      selected:
        window.location.hash.startsWith(Links.intern.team.href) ||
        window.location.hash === "/team",
    },
    {
      ...Links.intern.contact,
      selected:
        window.location.hash.startsWith(Links.intern.contact.href) ||
        window.location.hash === "/tarieven",
    },
  ];

  return (
    <HeaderMolecule
      logo="assets/icons/IJFysio_Primair_logo_paars_transparant-cropped.svg"
      links={links}
    />
  );
}
