import { useState } from "react";
import { ContainerAtom, HeadingH2Atom, ImageAtom } from "../../atoms";
import { JumbotronMolecule } from "../jumbotron";
import styles from "./styles.module.scss";

interface Props {
  src: string;
  size?: "icon" | "small" | "medium" | "large" | "xlarge" | "auto";
  alt: string;
  title: string;
  text?: string;
  color?: "lichtBlauw" | "paars" | "oranje";
  rtl?: boolean;
  noPadding?: boolean;
}

export function ImageJumbotronMolecule(props: Props) {
  const [windowWidth] = useState(window.innerWidth);
  const desktopBreakpoint = 16 * 50; // Should reference breakpoints.scss

  let className = styles.container;
  if (props.noPadding) className += `  ${styles[`container__noPadding`]}`;

  const icon = () => {
    let className = styles.icon;
    return (
      <div className={className}>
        <ImageAtom
          src={"assets/icons/IJFysio_High_five_cirkel_wit_paars_lijn.svg"}
          size={"xlarge"}
          alt={"High five icon"}
        />
      </div>
    );
  };

  const element = () => {
    let classNameImage = styles.imageContainer;
    if (props.rtl) classNameImage += ` ${styles.imageContainer__left}`;

    let classNameJumbo = styles.jumbotronContainer;
    if (!props.rtl) classNameJumbo += ` ${styles.jumbotronContainer__left}`;

    if (props.rtl || windowWidth <= desktopBreakpoint)
      return (
        <div className={className}>
          <ContainerAtom centeredHorizontal type="row">
            <div className={classNameImage}>
              <ImageAtom
                src={props.src}
                size={props.size ?? "auto"}
                alt={props.alt}
              />
              {icon()}
            </div>
            <div className={classNameJumbo}>
              <ContainerAtom type="columns">
                <div className={styles.title}>
                  <HeadingH2Atom children={props.title} color="zand" />
                </div>
                <JumbotronMolecule
                  text={props.text ?? undefined}
                  color={props.color}
                />
              </ContainerAtom>
            </div>
          </ContainerAtom>
        </div>
      );

    return (
      <div className={className}>
        <ContainerAtom centeredHorizontal type="row">
          <div className={classNameJumbo}>
            <ContainerAtom type="columns">
              <div className={styles.title}>
                <HeadingH2Atom children={props.title} color="zand" />
              </div>
              <JumbotronMolecule
                text={props.text ?? undefined}
                color={props.color}
              />
            </ContainerAtom>
          </div>
          <div className={classNameImage}>
            <ImageAtom
              src={props.src}
              size={props.size ?? "auto"}
              alt={props.alt}
            />
            {icon()}
          </div>
        </ContainerAtom>
      </div>
    );
  };

  return element();
}
