import styles from "./styles.module.scss";

interface Props {
  children: any;
  id?: string;
  color?: "default" | "zand";
  bold?: boolean;
}

export function HeadingH1Atom(props: Props) {
  let className = `${styles.heading} ${styles.heading__h1}`;

  if (props.color) className += ` ${styles[`heading__${props.color}`]}`;
  if (props.bold) className += ` ${styles[`heading__bold`]}`;

  return (
    <h1 id={props.id} className={className}>
      {props.children}
    </h1>
  );
}

