import { ButtonAtom, DividerAtom, ImageAtom, LabelAtom } from "../../atoms";
import styles from "./styles.module.scss";

interface Props {
  id: string;
  title: string;
  onClick: () => void;
  type: "default" | "primary";
}

interface IconButtonProps extends Props {
  icon: string;
  text?: string;
}

interface TextButtonProps extends Props {
  text: string;
}

const mapTypes = (
  type: string
): {
  buttonType?: "primary";
  dividerType?: "primary";
  labelType: "default" | "primary";
} => {
  switch (type) {
    case "primary":
      return {
        buttonType: "primary",
        labelType: "primary",
        dividerType: "primary",
      };

    default:
      return {
        buttonType: undefined,
        labelType: "default",
        dividerType: undefined,
      };
  }
};

function IconButton(props: IconButtonProps) {
  const { buttonType, dividerType, labelType } = mapTypes(props.type);

  let ClassNameIconButton = styles.button__content;
  let ClassNameContent = styles.button__content;

  return (
    <ButtonAtom
      id={props.id}
      type={buttonType}
      onClick={props.onClick}
      title={props.title}
    >
      <span className={styles.button}>
        {props.text && (
          <>
            <div className={ClassNameContent}>
              <LabelAtom id={props.id} type={labelType}>
                {props.text}
              </LabelAtom>
            </div>

            <div className={styles.button__divider}>
              <DividerAtom type={dividerType} />
            </div>
          </>
        )}

        <div className={ClassNameIconButton}>
          <ImageAtom src={props.icon} size={"small"} alt={props.icon} />
        </div>
      </span>
    </ButtonAtom>
  );
}

export function IconButtonMolecule(props: IconButtonProps) {
  return IconButton(props);
}

export function ButtonMolecule(props: TextButtonProps) {
  const { buttonType, labelType } = mapTypes(props.type);

  return (
    <ButtonAtom
      id={props.id}
      type={buttonType}
      onClick={props.onClick}
      title={props.title}
    >
      <span className={styles.button}>
        <div className={styles.button__content}>
          <LabelAtom id={props.id} type={labelType}>
            {props.text}
          </LabelAtom>
        </div>
      </span>
    </ButtonAtom>
  );
}
