import { Teksten } from "../../../content";
import { IImageText } from "../../interfaces/IJumbotronImage";
import { ImageJumbotronListMolecule } from "../../molecules";

export function OverSpecialistenOrganism() {
  const items: IImageText[] = [
    {
      src: "assets/images/DSC_7026.jpg",
      alt: "Foto_Paul",
      title: Teksten.paul.title,
      text: Teksten.paul.text,
      color: "lichtBlauw",
    },
    {
      src: "assets/images/DSC_6988.jpg",
      alt: "Foto_Shaquille",
      title: Teksten.shaquille.title,
      text: Teksten.shaquille.text,
      color: "lichtBlauw",
    },
    {
      src: "assets/icons/IJFysio_Beeldmerk_dieporanje_transparant.svg",
      alt: "IJFysio_logo",
      title: Teksten.william.title,
      text: Teksten.william.text,
      color: "lichtBlauw",
    },
  ];

  return <ImageJumbotronListMolecule items={items} />;
}
