import { useCallback, useEffect, useRef, useState } from "react";
import { ImageAtom } from "../../atoms";
import styles from "./styles.module.scss";

interface Props {
  items: JSX.Element[];
}

export function CarouselMolecule(props: Props) {
  const [currentIndex, setCurrentIndex] = useState<number>(0);
  const containerRef = useRef<HTMLUListElement | null | undefined>();
  const itemsRef = useRef<HTMLElement | null>();

  const infiniteScroll = useCallback(
    (to?: number) => {
      let newIndex = currentIndex;

      if (to !== undefined) {
        if (to < 0) newIndex = props.items.length - 1;
        else if (to > props.items.length - 1) newIndex = 0;
        else newIndex = to;
      } else if (currentIndex === props.items.length - 1) newIndex = 0;
      else newIndex++;

      containerRef.current!.scrollTo({
        behavior: "smooth",
        left: (itemsRef.current?.offsetWidth ?? 100) * newIndex,
      });

      setCurrentIndex(newIndex);
    },
    [currentIndex, props.items.length]
  );

  useEffect(() => {
    const interval = setInterval(() => {
      infiniteScroll();
    }, 15000);
    return () => clearInterval(interval);
  }, [infiniteScroll]);

  return (
    <div className={styles.container}>
      <div className={styles.listWrapper}>
        <ul
          ref={(el: HTMLUListElement) => (containerRef.current = el)}
          className={styles.list}
        >
          {props.items.map((item: JSX.Element, index: number) => {
            return (
              <li
                key={index}
                ref={(el: HTMLLIElement) => (itemsRef.current = el)}
                className={styles.item}
              >
                <div className={styles.content}>{item}</div>
              </li>
            );
          })}
        </ul>

        <div
          onClick={() => {
            infiniteScroll(currentIndex - 1);
          }}
          className={styles.button + " " + styles.button__previous}
        >
          <ImageAtom
            src={"assets/icons/IJFysio_Pijl_paars_wit.svg"}
            size={"medium"}
            alt={"previous"}
          />
        </div>

        <div
          onClick={() => infiniteScroll(currentIndex + 1)}
          className={styles.button + " " + styles.button__next}
        >
          <ImageAtom
            src={"assets/icons/IJFysio_Pijl_paars_wit.svg"}
            size={"medium"}
            alt={"next"}
          />
        </div>

        {/* <button
          onClick={() => infiniteScroll(currentIndex - 1)}
          className={styles.button + " " + styles.button__previous}
          type="button"
        >
          ➜
        </button>
        <button
          onClick={() => infiniteScroll(currentIndex + 1)}
          className={styles.button + " " + styles.button__next}
          type="button"
        >
          ➜
        </button> */}
      </div>
    </div>
  );
}
