import { HeadingH2Atom } from "../../atoms";
import { BackgroundBlockMolecule } from "../../molecules";

interface Props {
  slogan: string;
}

export function SloganOrganism(props: Props) {
  return (
    <BackgroundBlockMolecule
      children={<HeadingH2Atom children={props.slogan} color="zand" />}
      background={"oranje"}
    />
  );
}
