import styles from "./styles.module.scss";

interface Props {
  id: string;
  onClick: () => void;
  children: any;
  type?: "default" | "primary";
  title?: string;
}

export function ButtonAtom(props: Props) {
  let className: string = styles.button;

  if(props.type && props.type !== "default"){
    const style = styles[`button--${props.type}`];
    className += ` ${style}`;
  } 
  
  return (
    <button id={props.id} type="button" className={className} onClick={props.onClick} title={props.title}>
      {props.children}
    </button>
  );
}
