import { ContainerAtom, HeadingH3Atom, SpacerAtom } from "../../atoms";
import styles from "./styles.module.scss";

interface Props {
  titles: string[];
  lists: JSX.Element[];
}

export function FooterMolecule(props: Props) {
  return (
    <footer className={styles.fixed_footer}>
      <div className={styles.content}>
        <ContainerAtom centeredHorizontal type="row">
          {props.lists.map((item: any, index: number) => {
            return (
              <div key={item + "_" + index}>
                <HeadingH3Atom children={props.titles[index]} />
                <SpacerAtom space={8} />
                {item}
              </div>
            );
          })}
        </ContainerAtom>
      </div>
    </footer>
  );
}
