import { LinkAtom, ListAtom, ListItemAtom } from "../../atoms";
import { ILink } from "../../interfaces/ILink";

interface Props {
  links: ILink[];
  unstyled?: boolean;
  bold?: boolean;
}

export function ListMolecule(props: Props) {
  const { links } = props;

  return (
    <ListAtom unstyled={props.unstyled}>
      {links.map((item) => (
        <ListItemAtom key={item.text} bold={props.bold}>
          <LinkAtom {...item} />
        </ListItemAtom>
      ))}
    </ListAtom>
  );
}
