import { useState } from "react";
import { ContainerAtom } from "../../atoms";
import { TextAreaAtom } from "../../atoms/textarea";
import { ButtonMolecule } from "../button";
import { EmailInputMolecule } from "../text-input";
import styles from "./styles.module.scss";

export function ContactFormMolecule() {
  const [onderwerp, setOnderwerp] = useState<string>();
  const [mail, setMail] = useState<string>("");

  const sendMail = () => {
    if (onderwerp && mail) {
      const recipient = "info";
      const at = String.fromCharCode(64);
      const dotcom = "ij-fysio.nl";
      const mailTo = "mailto:";
      window.open(`${mailTo + recipient + at + dotcom}?subject=${onderwerp}&body=${mail}`);
    }
  };

  return (
    <div className={styles.container}>
      <ContainerAtom type="columns" centeredHorizontal centeredVertical>
        <EmailInputMolecule
          key={"onderwerp"}
          id={"onderwerp"}
          placeholder={"Onderwerp van uw mail"}
          onChange={(value: string) => setOnderwerp(value)}
        />
        <TextAreaAtom placeholder={"Uw mail"} value={mail} onChange={(value: string) => setMail(value)} />
        <div>
          <ButtonMolecule
            text={"Verstuur"}
            id={"verstuur"}
            title={"Verstuur"}
            onClick={() => sendMail()}
            type={"default"}
          />
        </div>
      </ContainerAtom>
    </div>
  );
}
