import { ContainerAtom, HeadingH2Atom, ImageAtom, TextAtom } from "../../atoms";
import styles from "./styles.module.scss";

interface Props {
  image?: string;
  title?: string;
  text?: string;
  extra?: JSX.Element;
  color?: "lichtBlauw" | "paars" | "oranje";
}

export function JumbotronMolecule(props: Props) {
  let className = styles.color;
  if (props.color) {
    const style = styles[`color--${props.color}`];
    className += ` ${style}`;
  }

  return (
    <div className={styles.container}>
      <div className={className}>
        <div className={styles.figure}>
          {props.image && (
            <div className={styles.image}>
              <ImageAtom src={props.image} size={"auto"} alt={props.image} />
            </div>
          )}
          {}
          <ContainerAtom type="flex" centeredHorizontal centeredVertical>
            {props.title && (
              <div className={props.color ? "" : styles.hoverTitle}>
                <HeadingH2Atom
                  children={props.title}
                  color={props.color && props.color === "lichtBlauw" ? "default" : "zand"}
                />
              </div>
            )}

            {/* <SpacerAtom space={4} /> */}
            <TextAtom color={props.color && props.color === "lichtBlauw" ? "default" : "zand"}>{props.text}</TextAtom>
            {props.extra}
          </ContainerAtom>
        </div>
      </div>
    </div>
  );
}
