import styles from "./styles.module.scss";

interface Props {
  children: any;
  background: "paars" | "oranje" | "lijnenOranje" | "lijnenPaars"
}

export function BackgroundBlockMolecule(props: Props) {
  let className = styles.container;
  if (props.background) className += `  ${styles[`container__${props.background}`]}`;

  return (
    <div className={className}>
      <div className={styles.content}>
        {props.children}
      </div>
    </div>
  );
}