import styles from './styles.module.scss';

interface Props {
    id: string;
    children: string | string[];
    for?: string;
    type: "default" | "primary" ;
    bold?: boolean;
}

export function LabelAtom(props: Props) {
    const styleType = styles[`label--${props.type}`];
    let className = `${styles.label} ${styleType}`;

    if (props.bold){
        const styleBold = styles[`label--bold`];
        className += ` ${styleBold}`;
    }

    return <label htmlFor={props.for} className={className}>{
        Array.isArray(props.children) ? props.children.map((child:string) => <p key={props.id} >{child}</p>) : props.children
    }</label>
}

