import { IExternalLink } from "../../interfaces/ILink";
import styles from "./styles.module.scss";

export function LinkAtom(props: IExternalLink) {
    let className = styles.link;
    if (props.selected) className += ` ${styles.link__selected}`
    if (props.bold) className += ` ${styles.link__bold}`

    const target = props.newWindow ? "_blank" : undefined;

    return <a onClick={props.onClick} target={target} className={className} href={props.href} lang={props.lang} title={props.title}>{props.text}</a>
}