import { Teksten } from "../../../content";
import { IImageText } from "../../interfaces/IJumbotronImage";
import { TeamSectionMolecule } from "../../molecules";

export function TeamSectionOrganism() {
  const team: IImageText[] = [
    {
      src: "assets/images/Naamloos-2.jpg",
      alt: "Foto Paul",
      title: Teksten.paul.title,
    },
    {
      src: "assets/images/Naamloos-1.jpg",
      alt: "Foto Shaquille",
      title: Teksten.shaquille.title,
    },
    {
      src: "assets/icons/IJFysio_Beeldmerk_dieporanje_transparant.svg",
      alt: "image",
      title: Teksten.william.title,
    },
  ];

  return <TeamSectionMolecule team={team} />;
}
