import styles from "../layouts.module.scss";

interface Props {
  header: JSX.Element;
  body: JSX.Element;
  footer?: JSX.Element;
}

export function DefaultLayout(props: Props) {
  let className = styles.layouts__page;
  if (props.footer) className += ` ${styles.layouts__page__margin}`;

  return (
    <div className={className}>
      {props.header}

      <div className={styles.layouts__page__body}>
        <div className={styles.layouts__page__content}>
          {/* <ContainerAtom centeredHorizontal type="columns"> */}
          {props.body}
          {/* </ContainerAtom> */}
        </div>
      </div>

      {props.footer}
    </div>
  );
}
