import { ImageJumbotronMolecule } from "..";
import { ContainerAtom } from "../../atoms";
import { IImageText } from "../../interfaces/IJumbotronImage";
import styles from "./styles.module.scss";

interface Props {
  items: IImageText[];
}

export function ImageJumbotronListMolecule(props: Props) {
  return (
    <div className={styles.container}>
      <ContainerAtom type="columns" centeredVertical centeredHorizontal>
        {props.items.map((item: IImageText, index: number) => {
          return (
            <div key={item.title + "_" + index} className={styles.row}>
              <ImageJumbotronMolecule
                size={"auto"}
                src={item.src}
                alt={item.alt}
                title={item.title}
                text={item.text}
                color={item.color}
                rtl={index % 2 === 0 ? true : false}
                noPadding
              />
            </div>
          );
        })}
      </ContainerAtom>
    </div>
  );
}
